@charset "UTF-8";

// =======================================
// Font Settings
// =======================================

// rem 단위 계산을 위한 html 사이즈 리셋
$html-fz: 10px;

// 본문(paragraph) 사이즈
$body-fz: 16px;

// 본문(paragraph) 기본 컬러
$base-color: #000;

// 기본 행간 비율
$line-height: 1.4;

// 본문 행간 비율
$base-line: $body-fz * 1.4;

// 적용 웹 폰트
$font-family-ko: 'Noto Sans KR', sans-serif;
$font-family-en: 'Noto Sans KR', Arial, sans-serif, serif;

// vw 계산용 기준 viewport 사이즈
$vw-viewport: 720 !default;


//color
$c-black: #000;
$c-gray-1: #141618;
$c-gray-2: #f4f5f8;
$c-gray-3: #2E3337;
$c-gray-4: #7C828B;
$c-gray-5: #B9C0C6;
$c-gray-6: #D8DDE2;
$c-gray-7: #F4F5F8;

$c-white: #fff;
$c-main: #3575F6;

$c-bg-1: #D2DFF9;
$c-bg-2: #F4F5F8;

$c-red: #EA463C;
$c-orange: #F09E3A;
$c-green: #18AA88;
$c-blue: #3575F6;
$c-skyblue: #D2DFF9;

